<template>
  <div class="splash_image" :style="{'background-image': 'url('+url+')'}">
  </div>
  <div @mousemove="handleMousemove" class="splash_image" :style="{'background-image': 'url('+url2+')',
    'transition-duration': trasition_duration_ms+'ms',
    opacity: image2_visible?1:0,
    cursor: button_visible?'auto':'none'}">
  </div>
  <router-link to='/' :style="{opacity: button_visible?1:0}" id="backlink">
    <i class="back-arrow bi-arrow-left-square-fill"></i>
  </router-link>
</template>

<script>
export default {
  data() {return {
    loop: null,
    url: "",
    url2: "",
    image2_visible: false,
    trasition_duration_ms: 3000,
    image_duration: 12000,
    search_term: 'https://source.unsplash.com/random/?ikea,interior,mountain,nature,winter,summer,fall,japan,europe,architecture,design',
    last_mouse_move: -1000,
    button_visible: false,
    back_button_loop: null,
  }},
  async mounted() {
    this.imageLoop()
    this.loop = setInterval(() => {
      this.imageLoop()
    }, this.image_duration * 2);
    this.back_button_loop = setInterval(() => {
      const new_val = +Date.now()-(this.last_mouse_move+3000)<0
      console.log(new_val)
      this.button_visible = new_val
    }, 100)
  },
  unmounted() {
    clearInterval(this.loop)
    clearInterval(this.back_button_loop)
  },
  methods: {
    handleMousemove() {
      this.last_mouse_move = +Date.now()
    },
    async imageLoop() {
      setTimeout(() => {
        fetch(this.search_term).then(
          (data) => {
            const img = new Image()
            img.src = data.url
            img.onload =() => {
              this.image2_visible=true
            }
            this.url2 = data.url
          }
        )
      }, 0);
      setTimeout(() => {
        fetch(this.search_term).then(
          (data) => {
            const img = new Image()
            img.src = data.url
            img.onload =() => {
              this.image2_visible=false
            }
            this.url = data.url
          }
        )
      }, this.image_duration);
    },
  },
}
</script>

<style>
.splash_image {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: -1;
  background-size: cover;
  transition-property: opacity;
  background-color: darkgrey;
  background-position: center center;
}
.back-arrow {
  position: absolute;
  top: 20px;
  left: 20px;
  font-size: 3em;
  color: black;
}
#backlink {
  transition-duration: 500ms;
}
body {
  overflow: hidden;
}
</style>